import React from "react";
import Table from "react-bootstrap/Table";
import CustomSelect from "../Select/customSelect";
import "./staticTable.css";

/**
 * A reusable table component focused only on presentation
 */
const StaticTable = ({
  columnHeaders, // Array of pre-processed column data objects
  handleSelectChange, // Callback when selection changes
  dropdownOptions = [], // Pre-processed dropdown options
}) => {
  return (
    <Table bordered className="table-light">
      <thead>
        <tr>
          <th className="header-styling">Column Name</th>
          <th className="header-styling">Attribute Name</th>
        </tr>
      </thead>
      <tbody>
        {columnHeaders.map((column, index) => {
          const { header, hasNLS, currentMapping, defaultLabel } = column;

          return (
            <tr key={index}>
              <td>{header}</td>
              <td>
                <CustomSelect
                  index={index}
                  selectedValue={currentMapping?.uiLabel || header}
                  onChange={(index, value) => {
                    handleSelectChange(header, value);
                  }}
                  size="lg"
                  className="w-100"
                  disabled={!hasNLS}
                  options={{
                    defaultLabel: defaultLabel,
                    list: dropdownOptions,
                  }}
                />
              </td>
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
};

export default StaticTable;
